import { background, color } from "storybook/internal/theming";
import { createCard } from "./Card";
import { getByLabelText } from "@storybook/test";

export default {
  title: "Component/Card",
  tags: ["autodocs"],
  render: ({ label, ...args }) => {
    // You can either use a function to create DOM elements or use a plain html string!
    // return `<div>${label}</div>`;
    return createCard({ label, ...args });
  },
  argTypes: {
    bodyTitle: { control: "text" },
    color: { control: "color" },
    background: { control: "color" },
    fontSize: { control: "text" },
  },
};

const Template = (args) => {
  const card = createCard(args);
  const container = document.createElement("div");
  container.appendChild(card);
  return container;
};

export const Default = Template.bind({});
Default.args = {
  bodyTitle: "Hello World",
  color: "black",
  background: "blue",
  fontSize: "1.9rem",
};

export const CardBox = Template.bind({});
CardBox.args = {
  bodyTitle: "Another Title",
  color: "yellow",
  background: "red",
  fontSize: "18px",
};
